
import { QuillEditor } from "@vueup/vue-quill";
import { defineComponent, ref } from "vue";

export default defineComponent({
  components: {},
  props: {
    bodyHtml: {
      type: String,
      required: true,
    },
  },
  setup(_, content) {
    const editor = ref(QuillEditor);
    const update = () => {
      const html = editor.value.getHTML();
      content.emit("updated", html);
    };
    const deleteClick = () => {
      content.emit("delete");
    };
    return {
      update,
      editor,
      deleteClick,
    };
  },
});
